import React from 'react'
import Content from '../../About/Components/Content';
import MapContent from '../../About/Components/MapContent';

const About = () => {
    return (
        <>
            <div className='flex-container-column' style={{paddingTop: '10px', marginBottom: '150px', marginTop: '50px'}}>
                <div className='container-content-center'>
                    <Content />
                    <MapContent />
                </div>
            </div>
        </>
    );
}

export default About;